@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --box-shadow-light: 0 0 .5rem #00000022;
  --box-shadow-heavy: 0 0 1rem #00000055;
  --border-radius-rounded: 1em;
  --border-radius-pill: 100vw;
  --border-radius: .2em;
  --bounce: cubic-bezier(0.5, 2.2, 0.6, 0.5);
}

:root.dark {
  --white: #000000;
  --black: #ffffff;
  --off-black: #e8f3ff;
  --off-white: #17191a;
  --light-color: #145DA0;
  --light-color2: #144064;
  --light-color3: #050A30;
  --dark-color: #D4F1F4;
  --dark-color2: #75E6DA;
  --dark-color3: #189AB4;
  --danger-color: #dd4d4d;
  --danger-color2: #be3535;
}

:root.light {
  --white: #ffffff;
  --black: #000000;
  --off-white: #e8f3ff;
  --off-black: #17191a;
  --dark-color: #050A30;
  --dark-color2: #144064;
  --dark-color3: #145DA0;
  --light-color: #D4F1F4;
  --light-color2: #75E6DA;
  --light-color3: #189AB4;
  --danger-color: #dd4d4d;
  --danger-color2: #be3535;
}


::-webkit-scrollbar {
  width: .8em;
  height: .8em;
}

::-webkit-scrollbar-track {
  background: var(--off-white);
}

::-webkit-scrollbar-thumb {
  background: var(--dark-color2);
  border-radius: 1em;
  border: var(--off-white) solid 2px;
  box-sizing: border-box;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--light-color);
}

@supports(scrollbar-width: none) {
  * {
    scrollbar-color: var(--light-color2) var(--dark-color2);
    scrollbar-width: thin;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto Slab', serif, sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
}

@media (prefers-reduced-motion: reduce) {
  * {
    opacity: 1 !important;
  }

  *,
  ::after,
  ::before {
    animation: none !important;
    transition: none !important;
  }

}

a {
  text-decoration: none;
  color: var(--dark-color2);
}

p, h1, h2, h3, h4, h5, h6, a {
  font-weight: 500;
  position: relative;
}

p {

  text-align: justify;
}

a:focus,
a:hover {
  color: var(--dark-color3);
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--off-white);
  color: var(--dark-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.app {
  opacity: 0;
  height: 100vh;
  padding: 1rem;
  overflow: auto;
  animation: fadeIn 500ms forwards;
}

.container {
  max-width: 1080px;
  margin: auto;
  padding: 2rem;
  display: flex;
}

.theme-btn {
  z-index: 2;
  background-color: var(--off-white);
  color: var(--off-black);
  border-radius: var(--border-radius-pill);
  box-shadow: var(--box-shadow-heavy);
  position: fixed;
  width: 4rem;
  height: 4rem;
  right: 3rem;
  bottom: 3rem;
  cursor: pointer;
  transition: color 100ms, background-color 100ms;
}

.theme-btn:focus,
.theme-btn:hover {
  background-color: var(--off-black);
  color: var(--off-white);
}

.profile-pic {
  display: inline;
  max-width: 300px;
  max-height: 300px;
  overflow: hidden;
  border-radius: var(--border-radius-pill);
  flex: 1 1 300px;
}

.profile-pic img {
  max-width: 300px;
  max-height: 300px;
  width: 100%;
  object-fit: cover;
  object-position: top;
}

.about-text {
  flex: 1 1 50%;
  text-align: justify;
}

.boids-text {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc(100% - 500px - 4rem);
  text-align: justify;
}

video {
  border-radius: var(--border-radius);
  max-width: 500px;
}


.card.click-card {
  position: relative;
}


.card.click-card:not(.active) {
  outline: solid 3px var(--light-color3);
}

.card.click-card:not(.active) .card-header {
  border: none;
}

.card.click-card .card-body p {
  margin-top: 3rem;
}

.card.click-card .card-header h2 {
  z-index: 1;
}

.card.click-card .level-wrapp {
  position: absolute;
  height: 100%;
  z-index: 0;
  inset: 0;
  margin: 0;
  transform-origin: top;
  transition: top 250ms, transform 250ms, height 250ms;
}

.card.click-card.active .level-wrapp {
  top: 5rem;
  height: 2rem;
  transform: scaleX(.95);
}
.frame {
    position: relative;
    outline: solid 4px var(--dark-color2);
    border-radius: var(--border-radius);
    padding: 2rem;
    margin-top: 4rem;
    margin-bottom: 3rem;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    flex-wrap: wrap;
    align-items: flex-start;
    box-sizing: border-box;
}

.frame .title {
    position: absolute;
    top: -1.5em;
    left: 1rem;
    background: var(--off-white);
    padding: 1rem;
    white-space: nowrap;
}

.frame:focus {
    outline: solid 4px var(--dark-color3);
}

@media screen and (max-width: 640px) {
    .frame .title {
        font-size: 1.4rem;
    }
}
canvas {
    position: relative;
    border-radius: var(--border-radius);
    max-width: 500px;
    width: 100%;
}
.card {
    background-color: var(--white);
    box-shadow: var(--box-shadow-light);
    border-radius: var(--border-radius);
    min-width: 150px;
    overflow: hidden;
}

.card-header:not(:last-child) {
    border-bottom: var(--off-black) solid 1px;
    padding: 1em;
}

.card-body:not(:last-child) {
    border-bottom: var(--off-black) solid 1px;
    padding: 1em;
}

.card-body.collapse {
    padding: 0;
    transition: opacity 250ms, height 250ms 250ms ease;
}

.active .card-body.collapse {
    transition: opacity 250ms, height 250ms ease;
}

.collapse-padding {
    padding: 1em;
}

.card-footer {
    padding: 1em;
}

.click-card {
    cursor: pointer;
    transition: transform 250ms var(--bounce);
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(50% - 4rem);
}

.click-card:hover,
.click-card:focus {
    transform: scale(1.0625);
    z-index: 1;
}
@keyframes fill {
    from {

        transform: scaleX(0);
    }

    to {
        transform: scaleX(1);
    }
}

.level-wrapp {
    position: relative;
    width: 100%;
    height: 2rem;
    background-color: var(--light-color3);
    border-radius: var(--border-radius);
    overflow: hidden;
    margin-top: 1rem;
    outline: solid 4px var(--light-color3);
}

.level-1 {
    position: absolute;
    inset: 0;
    background-color: var(--light-color2);
    transform: scaleX(0);
    transform-origin: left;
    animation: fill 1s ease-in forwards;
}

.level-2 {
    position: absolute;
    inset: 0;
    background-color: var(--light-color);
    transform: scaleX(0);
    transform-origin: left;
    animation: fill 1.5s 1s ease-out forwards;
}
